
.userTab {
  border-style: solid;
  border-color: gray;
  padding: 0px 5px 0px 5px;
}
.close{
    margin-left: 10px;
}
.tabroot{
    margin-left: 15px;
    height: 37px;
}
.btn {
    border-bottom-style: none;
}
.btn-group .btn:first-child{
    border-bottom-left-radius: 0;
}
.btn-group .btn:last-child{
    border-bottom-right-radius: 0;
}
