
.form-control {
  width: 75%;
}
#messageinput {
  margin-top: 10px;
  width: 100%;
  border-radius: 10px;
  background-color: rgb(243, 243, 243);
  border-style: solid;
  border-color: rgb(23, 162, 184);
  border-width: 2px;
  padding: 5px;
}
#messageinput input {
  display: inline-block;
}
#messageinput button{
    margin-left: 10px;
    margin-top: -5px;
    width:20%;
}
#chatlist {
  width: 100%;
  height: 500px;
  border-radius: 10px;
  background-color: rgb(243, 243, 243);
  border-style: solid;
  border-color: rgb(23, 162, 184);
  border-width: 2px;
  padding: 20px;
  overflow: auto;
}
ul {
  padding: 0px;
}
ul li {
  list-style-type: none;
}
